import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const demandData = wutil.makeRandomData({ cnt: 500, min: 0, max: 4000, onlyUp: false, inputData: [], diff: 100 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 0, max: 4000, onlyUp: false, demandData, diff: 100 });

const initialState = {
	aiData,
	demandData,
};

@observer
class Demand extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { demandData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Demand</h1>
						<LineGraph
							stores={this.props.stores}
							data={demandData}
							dataB={aiData}
							legend='Demand'
							legendB='AI prediction'
							width={1200}
							height={500}
							yMax={4000}
							yMin={0}
							showYTicks={1}
							yTicks='["4TW", "3TW", "2TW", "1TW", "0TW"]'
							unit='Mw'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Demand;
